import React from 'react';
import { GlobalComponent } from './components/GlobalComponent';
import { BrowserRouter as HashRouter, Route, Switch } from "react-router-dom";
import { Asignatura } from './components/Asignatura';
import { Tema } from './components/Tema';

function App() {
  return (
    <HashRouter basename={'https://sirkan98.github.io/study-app/'}>
      <Switch>
        <Route path="/" exact component={GlobalComponent} />
        <Route path="/asignatura/:name" exact component={Asignatura} />
        <Route path="/asignatura/:name/:nameTema" exact component={Tema} />
      </Switch>
    </HashRouter>
  );
}

export default App;
