import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useRouteMatch } from 'react-router';
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';
import warningIco from '../assets/img/warning.ico';

export const Asignatura = () => {

    let match = useRouteMatch();

    let nameAsignatura = match.params.name;

    const [preguntas, setPreguntas] = useState(null);

    const [informacionAsignatura, setInformacionAsignatura] = useState(JSON.parse(localStorage.getItem(nameAsignatura)))

    const [checked, setChecked] = useState(null);

    useEffect(() => {
        if (informacionAsignatura) {
            let object = [];
            for (let i = 0; i < informacionAsignatura.length; i++) {
                object.push(false);
            }
            setChecked(object);
        }

    }, [informacionAsignatura])

    const [inputTema, setInputTema] = useState('');

    const handleChangeInputTema = (e) => {
        setInputTema(e.target.value);
    }

    const handleSubmitForm = (e) => {
        e.preventDefault();

        if (!informacionAsignatura) {

            let tema = {
                name: inputTema
            }

            let informacionAsignaturaTemp = [];

            informacionAsignaturaTemp.push(tema);

            localStorage.setItem(nameAsignatura, JSON.stringify((informacionAsignaturaTemp)))

            setInformacionAsignatura(JSON.parse(localStorage.getItem(nameAsignatura)));

        } else {

            let longitud = informacionAsignatura.length;

            let tema = {
                name: inputTema
            }

            let informacionAsignaturaTemp = [];

            for (let i = 0; i < informacionAsignatura.length; i++) {
                informacionAsignaturaTemp.push(informacionAsignatura[i]);
            }

            informacionAsignaturaTemp.push(tema);

            localStorage.setItem(nameAsignatura, JSON.stringify((informacionAsignaturaTemp)))

            setInformacionAsignatura(JSON.parse(localStorage.getItem(nameAsignatura)));

        }
    }

    const refInputTema = useRef(null);

    const [mostrarPregunta, setMostrarPregunta] = useState(false);

    const [mostrarRespuesta, setMostrarRespuesta] = useState(false);

    const [preguntaRespuesta, setPreguntaRespuesta] = useState(null);

    const handlePregunta = () => {

        if (informacionAsignatura && informacionAsignatura.length > 0) {

            setMostrarRespuesta(false);

            let temp = informacionAsignatura.slice();

            let newTemp = temp[Math.floor(Math.random() * temp.length)];

            setPreguntaRespuesta(newTemp);

            setMostrarPregunta(true);

            let booleanResp = false;

            if (checked) {
                console.log("fes");
                for (let i = 0; i < checked.length; i++) {
                    if (checked[i]) {
                        booleanResp = true;
                        break;
                    }
                }
            }

            if (booleanResp) {

                console.log(informacionAsignatura);

                let object = [];
                for (let i = 0; i < informacionAsignatura.length; i++) {
                    if (checked[i]) {
                        object.push(JSON.parse(localStorage.getItem(nameAsignatura + "_" + informacionAsignatura[i].name)))
                    }
                }

                setMostrarPregunta(true);

                let objectTemp = [];

                for (let i = 0; i < object.length; i++) {
                    if (object[i]) {
                        objectTemp.push(...object[i])
                    }
                }

                console.log(objectTemp);
                setPreguntas(objectTemp);

                const randomElement = objectTemp[Math.floor(Math.random() * objectTemp.length)];

                if (randomElement) {
                    const pregResp = {
                        "pregunta": randomElement.pregunta,
                        "respuesta": randomElement.respuesta
                    }
    
                    setPreguntaRespuesta(pregResp);
                } else {
                    alert('No hay preguntas en los temas seleccionados.')
                }
            } else {
                alert("Tienes que seleccionar algún tema");
            }

        } else {
            alert("No tienes ninguna pregunta de esta asignatura.")
        }
    }

    const handleMostrarRespuesta = () => {
        setMostrarRespuesta(true);
    }

    const [mostrarTodasPreguntas, setMostrarTodasPreguntas] = useState(false)

    const handleClickMostrarTodasPreguntas = () => {
        if (!informacionAsignatura) {
            alert("No tienes ninguna pregunta de esta asignatura");
        } else {

            if (informacionAsignatura && informacionAsignatura.length > 0) {

                setMostrarRespuesta(false);

                let temp = informacionAsignatura.slice();

                let newTemp = temp[Math.floor(Math.random() * temp.length)];

                setPreguntaRespuesta(newTemp);

                let booleanResp = false;

                if (checked) {
                    for (let i = 0; i < checked.length; i++) {
                        if (checked[i]) {
                            booleanResp = true;
                            break;
                        }
                    }
                }

                if (booleanResp) {

                    console.log(informacionAsignatura);

                    let object = [];
                    for (let i = 0; i < informacionAsignatura.length; i++) {
                        if (checked[i]) {
                            object.push(JSON.parse(localStorage.getItem(nameAsignatura + "_" + informacionAsignatura[i].name)))
                        }
                    }

                    let objectTemp = [];

                    for (let i = 0; i < object.length; i++) {
                        if (object[i]) {
                            objectTemp.push(...object[i])
                        }
                    }

                    console.log(objectTemp);
                    setPreguntas(objectTemp);

                } else {
                    alert("Tienes que seleccionar algún tema");
                }

            } else {
                alert("No tienes ninguna pregunta de esta asignatura.")
            }

            setMostrarTodasPreguntas(!mostrarTodasPreguntas);
        }
    }

    const refPregunta = useMemo(() => Array(informacionAsignatura ? informacionAsignatura.length : 0).fill(null).map(i => React.createRef()), [informacionAsignatura]);

    const refRespuesta = useMemo(() => Array(informacionAsignatura ? informacionAsignatura.length : 0).fill(null).map(i => React.createRef()), [informacionAsignatura]);

    const refButton = useMemo(() => Array(informacionAsignatura ? informacionAsignatura.length : 0).fill(null).map(i => React.createRef()), [informacionAsignatura]);

    const editarPregunta = (j) => {

        refPregunta[j].current.classList.toggle('d-none');
        refRespuesta[j].current.classList.toggle('d-none');
        refButton[j].current.classList.toggle('d-none');

    }

    const handleBorrarAsignatura = () => {

        Swal.fire({
            title: '¿Estás seguro?',
            text: "Perderás todas las preguntas de esta asignatura y no podrás volver a recuperarlas.",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Borrar asignatura',
            cancelButtonText: 'Cancelar'
        }).then((result) => {
            if (result.isConfirmed) {
                Swal.fire(
                    'Asignatura borrada',
                    '',
                    'success'
                ).then(() => {

                    let asignaturasLocalStorage = JSON.parse(localStorage.getItem("asignaturas"));
                    console.log(asignaturasLocalStorage);

                    let newAsignaturasLocalStorage = [];

                    for (let i = 0; i < asignaturasLocalStorage.length; i++) {
                        if (asignaturasLocalStorage[i].name !== nameAsignatura) {
                            newAsignaturasLocalStorage.push({
                                "name": asignaturasLocalStorage[i].name
                            })
                        }
                    }

                    localStorage.removeItem(nameAsignatura);

                    localStorage.setItem("asignaturas", JSON.stringify(newAsignaturasLocalStorage));

                    for (let i = 0; i < window.localStorage.length; i++) {
                        let key = window.localStorage.key(i);
                        if (key.slice(0, nameAsignatura.length + 1) === nameAsignatura + "_") {
                            console.log("gdsgwef");
                            localStorage.removeItem(key);
                        }
                    }

                    window.location.replace("/");

                })
            }
        })

    }

    const [selectAll, setSelectAll] = useState(false);

    const handleSelectAll = () => {
        if (informacionAsignatura && informacionAsignatura.length > 0) {
            if (!selectAll) {
                setSelectAll(true);
                let object = [];
                for (let i = 0; i < informacionAsignatura.length; i++) {
                    object.push(true);
                }
                setChecked(object);
            } else {
                setSelectAll(false);
                let object = [];
                for (let i = 0; i < informacionAsignatura.length; i++) {
                    object.push(false);
                }
                setChecked(object);
            }
        } else {
            alert('No existe ningún tema.');
        }
    }

    useEffect(() => {
        if (selectAll) {

        }
    }, [selectAll])

    const toggleCheck = (i) => {
        const newState = [...checked];
        newState[i] = !checked[i];
        console.log(newState);
        setChecked(newState);
    }

    return (
        <div className="container mb-5">
            <h3 className="text-center mt-3">{nameAsignatura}</h3>
            <form onSubmit={handleSubmitForm}>
                <p>Añadir tema:</p>
                <input ref={refInputTema} type="text" className="form-control mb-3" value={inputTema} onChange={handleChangeInputTema} />
                <button className="btn btn-primary">Confirmar</button>
            </form>
            <div className="mt-3">
                <h4>Temas:</h4>
                <button className="btn btn-outline-info" onClick={handleSelectAll}>Seleccionar todos</button>
                {informacionAsignatura && checked ? (
                    informacionAsignatura.map(function (object, i) {
                        return (
                            <div key={i} className="form-check">
                                <input type="checkbox" className="align-middle mr-2" onChange={() => toggleCheck(i)}
                                    checked={checked[i] ? checked[i] : false}
                                />
                                <label className="form-check-label mt-2">
                                    <Link to={
                                        {
                                            pathname: '/asignatura/' + nameAsignatura + '/' + object.name
                                        }
                                    } className="btn btn-success align-middle">
                                        {object.name}
                                    </Link>
                                </label>
                            </div>
                        );
                    })
                ) : null}
            </div>
            <div className="alert alert-info mt-4">
                <button className="btn btn-secondary" onClick={handlePregunta}>Pregunta al azar de los temas seleccionados</button>
                {
                    mostrarPregunta ?
                        <>
                            <p className="mt-4">
                                <b>Pregunta:</b> {preguntaRespuesta.pregunta}
                            </p>
                            {mostrarRespuesta ?
                                <p>
                                    <b>Respuesta: </b>{preguntaRespuesta.respuesta}
                                </p> : null
                            }
                            <button className="btn btn-success" onClick={handleMostrarRespuesta}>Mostrar respuesta</button>

                        </> : null
                }
            </div>
            <div className="mb-2">
                <button className="btn btn-outline-dark" onClick={handleClickMostrarTodasPreguntas}>Mostrar / Ocultar las preguntas de los temas seleccionados</button>
            </div>
            {
                mostrarTodasPreguntas && preguntas ? (
                    <ol>
                        {
                            preguntas.map(function (object, i) {
                                return (
                                    <li key={i} data={object} className="mt-3">
                                        <b>Pregunta:</b> {object.pregunta}
                                        <div>
                                            <b>Respuesta:</b> {object.respuesta}
                                        </div>
                                    </li>
                                );
                            })
                        }
                    </ol>

                ) : null
            }
            <div>
                <button onClick={handleBorrarAsignatura} className="btn btn-danger">
                    <img src={warningIco} alt="warningIco" width="20px" />
                    <span> Borrar asignatura </span>
                    <img src={warningIco} alt="warningIco" width="20px" />
                </button>
            </div>
            <Link to="/" className="btn btn-outline-primary mt-2">Volver al menú</Link>
        </div >
    )
}

