import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useHistory, useRouteMatch } from 'react-router';
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';
import warningIco from '../assets/img/warning.ico';

export const Tema = () => {

    let history = useHistory();

    let match = useRouteMatch();

    let nameAsignatura = match.params.name;

    let nameTema = match.params.nameTema;

    const [informacionTema, setInformacionTema] = useState(JSON.parse(localStorage.getItem(nameAsignatura + "_" + nameTema)))

    const [inputPregunta, setInputPregunta] = useState('');

    const handleChangeInputPregunta = (e) => {
        setInputPregunta(e.target.value);
    }

    const [inputRespuesta, setInputRespuesta] = useState('');

    const handleChangeInputRespuesta = (e) => {
        setInputRespuesta(e.target.value);
    }

    const refInputPregunta = useRef(null);

    const handleSubmitForm = (e) => {

        e.preventDefault();

        if (inputPregunta === "") {
            return null;
        }

        if (!informacionTema) {
            let temaTemp = [];
            temaTemp.push({
                "pregunta": inputPregunta, "respuesta": inputRespuesta
            })
            localStorage.setItem(nameAsignatura + "_" + nameTema, JSON.stringify(temaTemp));
        } else {

            let asignaturasLocal = JSON.parse(localStorage.getItem(nameAsignatura + "_" + nameTema));

            asignaturasLocal.push({
                "pregunta": inputPregunta, "respuesta": inputRespuesta
            })

            localStorage.setItem(nameAsignatura + "_" + nameTema, JSON.stringify(asignaturasLocal))
        }

        setInformacionTema(JSON.parse(localStorage.getItem(nameAsignatura + "_" + nameTema)))

        setInputPregunta("");
        setInputRespuesta("");

        refInputPregunta.current.focus();
    }

    const [mostrarPregunta, setMostrarPregunta] = useState(false);

    const [mostrarRespuesta, setMostrarRespuesta] = useState(false);

    const [preguntaRespuesta, setPreguntaRespuesta] = useState(null);

    const handlePregunta = () => {

        if (informacionTema && informacionTema.length > 0) {

            setMostrarRespuesta(false);

            let temp = informacionTema.slice();

            let newTemp = temp[Math.floor(Math.random() * temp.length)];

            setPreguntaRespuesta(newTemp);

            setMostrarPregunta(true);

        } else {
            alert("No tienes ninguna pregunta de esta asignatura.")
        }
    }

    const handleMostrarRespuesta = () => {
        setMostrarRespuesta(true);
    }

    const [mostrarTodasPreguntas, setMostrarTodasPreguntas] = useState(false)

    const handleClickMostrarTodasPreguntas = () => {
        if (!informacionTema) {
            alert("No tienes ninguna pregunta de este tema");
        } else {
            setMostrarTodasPreguntas(!mostrarTodasPreguntas);
        }
    }

    const eliminarPregunta = (j) => {

        let response = window.confirm("¿Quieres eliminar la pregunta?");

        if (!response) {
            return;
        }

        let tempArray = [];
        for (let i = 0; i < informacionTema.length; i++) {
            if (i !== j) {
                tempArray.push(informacionTema[i]);
            }
        }

        localStorage.setItem(nameAsignatura + "_" + nameTema, JSON.stringify(tempArray));

        setInformacionTema(JSON.parse(localStorage.getItem(nameAsignatura + "_" + nameTema)));

    }

    const refPregunta = useMemo(() => Array(informacionTema ? informacionTema.length : 0).fill(null).map(i => React.createRef()), [informacionTema]);

    const refRespuesta = useMemo(() => Array(informacionTema ? informacionTema.length : 0).fill(null).map(i => React.createRef()), [informacionTema]);

    const refButton = useMemo(() => Array(informacionTema ? informacionTema.length : 0).fill(null).map(i => React.createRef()), [informacionTema]);

    const editarPregunta = (j) => {

        refPregunta[j].current.classList.toggle('d-none');
        refRespuesta[j].current.classList.toggle('d-none');
        refButton[j].current.classList.toggle('d-none');

    }

    const handleClickConfirmarCambio = (j) => {

        let tempArray = [];
        for (let i = 0; i < informacionTema.length; i++) {
            if (i !== j) {
                tempArray.push(informacionTema[i]);
            } else {
                let pregunta;
                let respuesta;

                if (refPregunta[j].current.value == '') {
                    pregunta = informacionTema[i].pregunta;
                } else {
                    pregunta = refPregunta[j].current.value;
                }

                if (refRespuesta[j].current.value == '') {
                    respuesta = informacionTema[i].respuesta;
                } else {
                    respuesta = refRespuesta[j].current.value;
                }

                let infTemp = {
                    pregunta,
                    respuesta
                }

                tempArray.push(infTemp);

                refPregunta[j].current.value = "";
                refRespuesta[j].current.value = "";
            }
        }

        localStorage.setItem(nameAsignatura + "_" + nameTema, JSON.stringify(tempArray));

        setInformacionTema(JSON.parse(localStorage.getItem(nameAsignatura + "_" + nameTema)));

    }

    const handleBorrarTema = () => {

        Swal.fire({
            title: '¿Estás seguro?',
            text: "Perderás todas las preguntas de esta tema y no podrás volver a recuperarlas.",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Borrar tema',
            cancelButtonText: 'Cancelar'
        }).then((result) => {
            if (result.isConfirmed) {
                Swal.fire(
                    'Tema borrado',
                    '',
                    'success'
                ).then(() => {

                    localStorage.removeItem(nameAsignatura + "_" + nameTema);

                    let asignatura = JSON.parse(localStorage.getItem(nameAsignatura));

                    let asignaturaTemp = [];

                    for (let i = 0; i < asignatura.length; i++) {

                        if (asignatura[i].name === nameTema) {

                        } else {
                            asignaturaTemp.push({
                                "name": asignatura[i].name
                            });
                        }
                    }

                    localStorage.setItem(nameAsignatura, JSON.stringify(asignaturaTemp));

                    window.location.replace("/");

                })
            }
        })


    }

    return (
        <div className="container mb-5">
            <h3 className="text-center mt-3">{nameAsignatura}</h3>
            <h5 className="text-center mt-3">{nameTema}</h5>
            <p>Número de preguntas: {informacionTema ? informacionTema.length : 0}</p>
            <form onSubmit={handleSubmitForm}>
                <p>Añadir pregunta:</p>
                <input ref={refInputPregunta} type="text" className="form-control mb-3" value={inputPregunta} onChange={handleChangeInputPregunta} />
                <p>Añadir respuesta:</p>
                <input type="text" className="form-control mb-3" value={inputRespuesta} onChange={handleChangeInputRespuesta} />
                <button className="btn btn-primary" onClick={handleSubmitForm}>Confirmar</button>
            </form>
            <div className="alert alert-info mt-4">
                <button className="btn btn-secondary" onClick={handlePregunta}>Pregunta al azar</button>
                {
                    mostrarPregunta ?
                        <>
                            <p className="mt-4">
                                <b>Pregunta:</b> {preguntaRespuesta.pregunta}
                            </p>
                            {mostrarRespuesta ?
                                <p>
                                    <b>Respuesta: </b>{preguntaRespuesta.respuesta}
                                </p> : null
                            }
                            <button className="btn btn-success" onClick={handleMostrarRespuesta}>Mostrar respuesta</button>

                        </> : null
                }
            </div>
            <div className="mb-2">
                <button className="btn btn-outline-dark" onClick={handleClickMostrarTodasPreguntas}>Mostrar / Ocultar todas las preguntas</button>
            </div>
            {mostrarTodasPreguntas ? (
                <ol>
                    {
                        informacionTema.map(function (object, i) {
                            return (
                                <li key={i} data={object} className="mt-3">
                                    <b>Pregunta:</b> {object.pregunta}
                                    <div>
                                        <b>Respuesta:</b> {object.respuesta}
                                    </div>
                                    <button className="btn btn-danger mr-2 mt-2" onClick={() => eliminarPregunta(i)}>Eliminar pregunta</button>
                                    <button className="btn btn-warning mt-2" onClick={() => editarPregunta(i)}>Editar pregunta</button>
                                    <form className="mt-2" onSubmit={(e) => (e.preventDefault(), handleClickConfirmarCambio(i))}>
                                        <input ref={refPregunta[i]} type="text" className="form-control mb-2 d-none" placeholder="Pregunta..." />
                                        <input ref={refRespuesta[i]} type="text" className="form-control mb-3 d-none" placeholder="Respuesta..." />
                                        <button ref={refButton[i]} className="btn btn-success d-none" onClick={(e) => (e.preventDefault(), handleClickConfirmarCambio(i))}>Confirmar cambio</button>
                                    </form>
                                </li>
                            );
                        })
                    }
                </ol>

            ) : null}
            <div>
                <button onClick={handleBorrarTema} className="btn btn-danger">
                    <img src={warningIco} alt="warningIco" width="20px" />
                    <span> Borrar tema </span>
                    <img src={warningIco} alt="warningIco" width="20px" />
                </button>
            </div>
            <button className="btn btn-outline-primary mt-2" onClick={history.goBack}>Volver a la asignatura</button>
        </div>
    )
}
